import "./Tabs.css";

const Tabs = ({active,onClick,tabs,tabSize}) => {
    return (
        <div className="tabs row">
                {tabs.map(tab=>
                    <div 
                        key={tab}
                        className={`col ${tabSize} ${active===tab?"active-tab":"tab"}`}
                        onClick={()=>onClick(tab)}
                    >
                        {tab}
                    </div>
                )}
        </div>  
    );
};
  
export default Tabs;