import React, { useEffect, useState } from 'react';
import DocumentMeta from 'react-document-meta';
import axios from 'axios';
import utils from './utils'
import PageHeader from "./components/PageHeader/PageHeader";
import Card from "./components/Card/Card";
import Home from "./components/Home/Home";
import Calendar from "./components/Calendar/Calendar"
import Tabs from "./components/Tabs/Tabs";
import './App.css';

async function getLocation(setLocation) {
  if (navigator.geolocation) {
    navigator.geolocation.getCurrentPosition(setLocation,error);
  }

  function error(error){
    console.log(error);
  }

}

const  App = () => {
  const [selected,setSelected] = useState(utils.dateFromUrl(window.location));
  const [activeTab,setActiveTab] = useState(utils.tabFromUrl(window.location));
  const [location,setLocation] = useState();
  const [localWeather,setLocalWeather] = useState();
  const [meta,setMeta] = useState({title:selected.toLocaleString().split(", ")[0]});

  useEffect(()=>{
    setMeta({title: selected.toLocaleString().split(", ")[0]});
    const day = selected.getDate();
    const dayName = utils.days[selected.getDay()];
    const month = utils.months[selected.getMonth()];
    const {days,months,years,firstDateWasLater} = utils.timeDiff(selected);
    const totalDays = utils.timeDiffDays(selected);
    const monthNumber = selected.getMonth()+1;
    const year = selected.getFullYear().toString();
    const selectedDate = [selected.getDate(),selected.getMonth(),selected.getFullYear()].join('');
    const currentDate = [new Date().getDate(),new Date().getMonth(),new Date().getFullYear()].join('');

    const dateConfig = `${day} ${month}, ${year} (${year}/${monthNumber}/${day} or ${day}-${monthNumber}-${year[year.length-2]}${year[year.length-1]})`

    if(selectedDate===currentDate){
      document.getElementById("meta-description").content=
      `Today is ${dateConfig}, ${dayName}. Use the date picker and other date tools to learn more about what happened on the selected day. {president} was the Us president and {artist} - {song} was the most popular song.`
    }
    else{
      if(firstDateWasLater){
        document.getElementById("meta-description").content=
        `${dateConfig} was ${dayName} ${years} ${years===1?"year":"years"}, ${months} ${months===1?"month":"months"} and ${days} ${days===1?"day":"days"} ago, a total of ${totalDays} ${totalDays===1?"day":"days"} ago.`
      }
      else {
        document.getElementById("meta-description").content=
        `There are ${years} ${years===1?"year":"years"}, ${months} ${months===1?"month":"months"} and ${days} ${days===1?"day":"days"} until ${dateConfig}, ${dayName}. By then we are expecting {predictions}.`
      }
    }
    
    },[selected])

  

  useEffect(()=>{
    async function setCurrentLocation(position) {
      if(location) return;
      var GEOCODING = `https://api.bigdatacloud.net/data/reverse-geocode-client?latitude=${position.coords.latitude}&longitude=${position.coords.longitude}&localityLanguage=en`;
      const res = await axios.get(GEOCODING);
      const weatherRes = await axios.get(`https://api.openweathermap.org/data/2.5/weather?lat=${position.coords.latitude}&lon=${position.coords.longitude}&appid=123c202ae147ce1a36f513e0a54fd90f`)
      weatherRes.data.sys.country="US"?
        setLocalWeather(
          [
            Math.floor(weatherRes.data.main.temp * 9/5 - 459.67)+"°F",
            weatherRes.data.weather[0].main,
            "H:"+Math.floor(weatherRes.data.main.temp_max * 9/5 - 459.67)+"°F",
            "L:"+Math.floor(weatherRes.data.main.temp_min * 9/5 - 459.67)+"°F"
          ].join(" ")):
        setLocalWeather(
          [
            Math.floor(weatherRes.data.main.temp - 273.15)+"°C",
            weatherRes.data.weather[0].main,
            "H:"+Math.floor(weatherRes.data.main.temp_max - 273.15)+"°C",
            "L:"+Math.floor(weatherRes.data.main.min - 273.15)+"°C"
          ].join(" "))

      setLocation([res.data.city,res.data.principalSubdivision].join(", "));
    }
    if(!location){
      getLocation(setCurrentLocation);
    }
  },[location])

  const tabComponents = {
    "Home":<Home selected={selected} setSelected={setSelected}/>,
    "Calendar":<Calendar/>
  }

  return (
    <div className="App">
      <DocumentMeta {...meta}/>
      <Card
        border={false}
        title={<PageHeader selected={selected} location={location} weather={localWeather}/>}
        tabs={<Tabs active={activeTab} onClick={setActiveTab} tabs={Object.keys(tabComponents)} tabSize={'large-tab'}/>}
        content={tabComponents[activeTab]}
        wholePage={true}
      />
    </div>
  );
}

export default App;
